// 
// preloader.scss
//

//Core files
@import "./node_modules/bootstrap/scss/functions";
@import "./node_modules/bootstrap/scss/variables";
@import "variables";

// pace

.pace {
	-webkit-pointer-events: none;
	pointer-events: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	user-select: none;

    .pace-progress {
        background: linear-gradient(to right, 
            $primary    20%, 
            $success    40%, 
            $info       60%, 
            $warning    80%, 
            $danger     100%
        );

        position: fixed;
        z-index: 2000;
        top: 0;
        right: 100%;
        width: 100%;
        height: 3px;
    }
}

.pace-inactive {
    .pace-progress{
	    display: none;
    }
}